@font-face {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local("PT Sans"), url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
}
@font-face {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: local("PT Sans Bold"), url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff");
}
html {
  box-sizing: border-box;
  background-color: #f2f2f2;
}
*,
*::before,
*::after {
  box-sizing: inherit;
}
body {
  max-width: 1200px;
  min-width: 320px;
  margin: 0 auto;
  padding: 0;
  font-family: "PT Sans", "Arial", "Helvetica", sans-serif;
  font-size: 14px;
  line-height: 21px;
  font-weight: normal;
  text-transform: uppercase;
  color: #333333;
  background: #ffffff;
  box-shadow: 0 5px 15px 0 rgba(0, 1, 1, 0.2);
}
@media (max-width: 767px) {
  br {
    content: "";
  }
  br::after {
    content: "\00a0";
  }
}
img {
  max-width: 100%;
  height: auto;
}
.button {
  display: block;
  padding: 15px;
  font-family: inherit;
  font-size: 21px;
  font-weight: bold;
  text-decoration: none;
  text-transform: uppercase;
  text-align: center;
  color: #ffffff;
  background-color: #81b3d2;
  border: none;
  cursor: pointer;
}
.button:hover {
  background-color: #669ec0;
}
.button:active {
  color: rgba(255, 255, 255, 0.3);
  background-color: #5496bd;
}
.button:disabled {
  background-color: #e5e5e5;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.page-header__logo {
  position: absolute;
  left: 50%;
  z-index: 1;
  height: 83px;
  width: 102px;
  transform: translateX(-50%);
}
@media (min-width: 768px) {
  .page-header__logo {
    top: 0;
  }
}
@media (min-width: 1200px) {
  .page-header__logo {
    width: 188px;
    background-size: cover;
    background-position: center 0;
  }
}
.main-nav__list {
  position: relative;
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 0;
  list-style: none;
}
.main-nav__list::before {
  content: "";
  position: absolute;
  bottom: 0;
  height: 7px;
  width: 100%;
  box-shadow: inset 0 -4px 4px 0 rgba(0, 0, 0, 0.1);
}
@media (min-width: 768px) {
  .main-nav__list {
    flex-direction: row;
    flex-wrap: wrap;
  }
  .main-nav__list::before {
    content: none;
  }
}
.main-nav__item {
  box-shadow: 0 -1px 0 0 rgba(1, 1, 1, 0.2);
}
.main-nav__item:first-child {
  box-shadow: none;
}
@media (min-width: 768px) {
  .main-nav__item {
    width: 20%;
    box-shadow: none;
  }
  .main-nav__item:nth-child(2) {
    margin-right: auto;
  }
}
.main-nav__link {
  display: block;
  min-height: 55px;
  padding-top: 9px;
  padding-bottom: 7px;
  font-size: 18px;
  font-weight: bold;
  line-height: 40px;
  text-align: center;
  text-decoration: none;
  background-color: #81b3d2;
  color: #ffffff;
  box-shadow: inset 0 1px 0 0 rgba(255, 255, 255, 0.2);
}
.main-nav__link:hover {
  background-color: #669ec0;
}
.main-nav__link:active {
  color: rgba(255, 255, 255, 0.3);
  background-color: #5496bd;
}
.main-nav__link:disabled {
  background-color: #e5e5e5;
}
.main-nav__item:last-child .main-nav__link {
  padding-bottom: 6px;
}
@media (min-width: 768px) {
  .main-nav__link {
    min-height: 56px;
    padding-top: 16px;
    padding-bottom: 7px;
    font-size: 14px;
    line-height: 26px;
    background-color: #ffffff;
    color: #000000;
    box-shadow: none;
  }
  .main-nav__link:hover {
    color: #81b3d2;
    background-color: white;
  }
  .main-nav__link:active {
    color: #81b3d2;
    background-color: white;
  }
  .main-nav__link:disabled {
    color: rgba(0, 0, 0, 0.3);
  }
  .main-nav__link--active {
    color: #766357;
  }
  .main-nav__link--active:hover {
    color: #604e43;
    background-color: #ffffff;
  }
  .main-nav__link--active:disabled {
    color: rgba(118, 99, 87, 0.3);
  }
}
.main-nav__toggle {
  position: absolute;
  top: 17px;
  right: 20px;
  display: block;
  width: 63px;
  height: 55px;
  background: none;
  border: none;
  cursor: pointer;
}
.main-nav--closed .main-nav__toggle {
  width: 23px;
  height: 22px;
  background: url("../img/icon-menu.svg") no-repeat center center;
}
.main-nav--opened .main-nav__toggle {
  width: 23px;
  height: 21px;
  background: url("../img/icon-cross.svg") no-repeat center center;
}
@media (max-width: 767px) {
  .main-nav--closed .main-nav__item {
    display: none;
  }
  .main-nav--nojs .main-nav__toggle {
    display: none;
  }
  .main-nav--nojs .main-nav__item {
    display: block;
  }
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}
.main-nav__image {
  display: block;
  width: 100%;
}
